:root {
  --container-bg-color: #333;
  --left-bg-color: rgba(223, 39, 39, 0.7);
  --left-button-hover-color: rgba(161, 11, 11, 0.3);
  --middle-bg-color: rgba(43, 43, 43, 0.8);
  --middle-button-hover-color: rgba(92, 92, 92, 0.3);
  --right-bg-color: rgba(223, 223, 108, 0.8);
  --right-button-hover-color: rgba(255, 255, 0, 0.3);
  --hover-width: 50%;
  --other-width: 25%;
  --other-other-width: 25%;
  --speed: 1000ms;
}

html, body, #root {
  padding:0;
  margin:0;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

h1 {
  font-size: 2.5rem;
  color: #fff;
  position: absolute;
  left: 50%;
  top: 20%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  white-space: nowrap;
}

.button {
  display: block;
  position: absolute;
  left: 50%;
  top: 40%;
  height: 2.5rem;
  padding-top: 1.3rem;
  width: 15rem;
  text-align: center;
  color: #fff;
  border: #fff solid 0.2rem;
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.split.left .button:hover {
  background-color: var(--left-button-hover-color);
  border-color: var(--left-button-hover-color);
}

.split.middle .button:hover {
  background-color: var(--middle-button-hover-color);
  border-color: var(--middle-button-hover-color);
}

.split.right .button:hover {
  background-color: var(--right-button-hover-color);
  border-color: var(--right-button-hover-color);
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  background: var(--container-bg-color);
}

.split {
  position: absolute;
  width: 33%;
  height: 100%;
  overflow: hidden;
}

.split.left {
  width: 34%;
}

.split.left {
  left:0;
  background: url('https://images.pexels.com/photos/46274/pexels-photo-46274.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260') center center no-repeat;
  background-size: cover;
  z-index: 2;
}

.split.left:before {
  position:absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: var(--left-bg-color);
}

.split.middle {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: url('https://image.ibb.co/m3ZbRb/programmer.png') center center no-repeat;
  background-size: cover;
  z-index: 1;
}

.split.middle:before {
  position:absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: var(--middle-bg-color);
}

.split.right {
  right:0;
  background: url('http://aswadgroup.com/wp-content/uploads/2018/05/636311326049773956-UC.jpg') center center no-repeat;
  background-size: cover;
  z-index: 2;
}

.split.right:before {
  position:absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: var(--right-bg-color);
}

.split.left, .split.right, .split.middle:before, .split.middle, .split.right:before, .split.left:before {
  -webkit-transition: var(--speed) all ease-in-out;
  -o-transition: var(--speed) all ease-in-out;
  transition: var(--speed) all ease-in-out;
}

.hover-left .left {
  width: var(--hover-width);
}

.hover-left .middle {
  width: var(--hover-width);
}

.hover-left .right {
  width: var(--other-width);
}

.hover-left .middle:before .right:before {
  z-index: 20;
}

.hover-middle .middle {
  width: 33%;
}

.hover-middle .left {
  width: 34%;
}

.hover-middle .right {
  width: 33%;
}

.hover-middle .left:before .right:before {
  z-index: 20;
}

.hover-right .right {
  width: var(--hover-width);
}

.hover-right .middle {
  width: var(--hover-width);
}

.hover-right .left {
  width: var(--other-width);
}

.hover-right .left:before .middle:before {
  z-index: 20;
}

@media(max-width: 800px) {
  h1 {
    font-size: 1.5rem;
  }

  .button {
    width: 12rem;
  }
}

@media(max-height: 700px) {
  .button {
    top: 70%;
  }
}



